.MyVolunteerList {
  display: flex;
  justify-content: right;
  align-items: right;
  margin: 0px;
  width: 70%;
}

.MyVolunteerList > .MyVolunteerList_Box {
  display: flex;
  justify-content: right;
  align-items: right;
  width: 100%;
  height: 100%;
  line-height: 130px;
  border: 1px #eeeff1 solid;
  border-radius: 2px;
}

.MyVolunteerList > .MyVolunteerList_Box > .MyVolunteerList_left {
  width: 100%;
}

.MyVolunteerList > .MyVolunteerList_Box > .MyVolunteerList_left > .MyVolunteerList_top {
  display: flex;
  height: 32%;
  margin-top: 10px;
}

.MyVolunteerList > .MyVolunteerList_Box > .MyVolunteerList_left > .MyVolunteerList_top > .MyVolunteerList_user_img {
  width: 17%;
  height: 50px;
  margin: auto;
  margin-left: 15px;

}

